import { NavLink } from 'react-router-dom';

export function Navigation() {
	function getClass(isActive: boolean) {
		return isActive ? 'link navigation__item active' : 'link navigation__item';
	}
	return (
		<nav className="navigation">
			<NavLink className={({ isActive }) => getClass(isActive)} to="/"></NavLink>
			<NavLink className={({ isActive }) => getClass(isActive)} to="/textbook"></NavLink>
			<NavLink className={({ isActive }) => getClass(isActive)} to="/audiocall"></NavLink>
			<NavLink className={({ isActive }) => getClass(isActive)} to="/sprint"></NavLink>
			<NavLink className={({ isActive }) => getClass(isActive)} to="/statistics"></NavLink>
		</nav>
	);
}
